import React from 'react'
import { Link } from 'react-router-dom'

export default function FeaturedItemsItemDetails(props) {
  return (
    <div className="row justify-content-center">
              <div className="col-8 col-md-10 card">
                <img
                  src={props.img}
                  alt="img"
                  className="img-fluid small-feature-photo w-100 pt-2"
                />
                <div className="d-flex">
                  <h6 className="card-title">
                    {props.title}
                  </h6>
                  <Link
                    to={props.link}
                    className="card-link ml-auto"
                  >
                    More info
                  </Link>
                </div>
              </div>
            </div>
  )
}
